import { Component, OnInit } from '@angular/core';
import { NewsNoticeService } from './news-notice-detail-service';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-news-notice-detail',
  templateUrl: './news-notice-detail.component.html',
  styleUrls: ['./news-notice-detail.component.scss'],
  providers: [NewsNoticeService]
})
export class NewsNoticeDetailComponent implements OnInit {
  id: number;
  news = [];
  environment = environment;
  constructor(private http: HttpClient, private service: NewsNoticeService, private activatedRoute: ActivatedRoute, private router: Router) {
    this.activatedRoute.params.subscribe((params: Params) => {
      let id = params['id'];
      this.service.getNewsById(id).subscribe((data: any) => {
        this.news = data;
      })
    });
  }

  ngOnInit() {
  }

}
