import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { VillageProfileService } from './village-profile.service';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-village-profile',
  templateUrl: './village-profile.component.html',
  styleUrls: ['./village-profile.component.scss'],
  providers: [VillageProfileService]
})
export class VillageProfileComponent implements OnInit {
  environment = environment;
  villageProfile = [];
  villages = [];
  image = "";
  villageNameInEnglish = "";
  descriptionInEnglish = "";
  isVillageDetailsOpen = false;
  constructor(private http: HttpClient, private service: VillageProfileService) {
    this.service.getVillageProfile().subscribe((data: any) => {
      this.villageProfile = data;
    });

    this.service.getVillages().subscribe((data: any) => {
      this.villages = data;
    });
  }

  ngOnInit() {
  }
  displayVillageDetails(event: MouseEvent, image, villageNameInEnglish, descriptionInEnglish) {
    this.villageNameInEnglish = villageNameInEnglish;
    this.image = image;
    this.descriptionInEnglish = descriptionInEnglish;
    this.isVillageDetailsOpen = true;
    event.preventDefault();
  }
}
