import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { IntroductionService } from './introduction.service'
import { Router, ActivatedRoute, Params } from '@angular/router';
import { environment } from 'src/environments/environment.prod';
@Component({
  selector: 'app-brief-introduction',
  templateUrl: './brief-introduction.component.html',
  styleUrls: ['./brief-introduction.component.scss'],
  providers: [IntroductionService]
})
export class BriefIntroductionComponent implements OnInit {
  introduction = [];
  id: number;
  environment = environment;
  constructor(private http: HttpClient, private service: IntroductionService, private activatedRoute: ActivatedRoute, private router: Router) {
    this.activatedRoute.params.subscribe((params: Params) => {
      let id = params['id'];
      this.service.getIntroductionById(id).subscribe((data: any) => {
        this.introduction = data;
      })
    });
  }

  ngOnInit() {
  }
}
