import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ContactUsService } from './contact-us.service';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';


@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.scss'],
  providers: [ContactUsService]
})
export class ContactUsComponent implements OnInit {
  public aFormGroup: FormGroup;

  public siteKey = '6LdHQKUUAAAAAB04AKVSvGPO3HFqUjxmDhP1Y9Fl';
  public captchaIsLoaded = false;
  public captchaSuccess = false;
  public captchaIsExpired = false;
  public captchaResponse?: string;

  public theme: 'light';
  public size: 'compact';
  public lang = 'en';
  public type: 'image';
  public useGlobalDomain: boolean = false;

  sentContact = false;
  constructor(private http: HttpClient, private service: ContactUsService, private router: Router, private formBuilder: FormBuilder) {
  }

  ngOnInit() {
    this.aFormGroup = this.formBuilder.group({
      recaptcha: ['', Validators.required]
    });
  }


  addContactForm = new FormGroup({
    'contactDetail': new FormGroup({
      'name': new FormControl(''),
      'email': new FormControl(''),
      'subject': new FormControl(''),
      'typeMessage': new FormControl(''),
      'message': new FormControl(''),
      'feedback': new FormControl('')
    })
  });

  submitContactForm(fg: FormGroup) {
    var name = fg.value.contactDetail.name;
    var email = fg.value.contactDetail.email;
    var subject = fg.value.contactDetail.subject;
    var typeMessage = fg.value.contactDetail.typeMessage;
    var message = fg.value.contactDetail.message;
    let type = fg.value.contactDetail.feedback;

    var contactUsDetails = {
      name, email, subject, type, message, typeMessage
    }

    if (this.captchaSuccess == true) {
      this.service.createContactUs(contactUsDetails).subscribe((response) => {
        if (response.status == 200) {
          this.sentContact = true;
          fg.reset('');
        }
      });
    }
  }
  handleSuccess(captchaResponse: string): void {
    this.captchaSuccess = true;
    this.captchaResponse = captchaResponse;
    this.captchaIsExpired = false;
  }
}
