import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IndexComponent } from './index/index.component';
import { BriefIntroductionComponent } from './brief-introduction/brief-introduction.component';
import { VillageProfileComponent } from './village-profile/village-profile.component';
import { OrganizationStructureComponent } from './organization-structure/organization-structure.component';
import { CommitteesComponent } from './committees/committees.component';
import { ActLawComponent } from './central-act-law-and-directives/act-law.component';
import { LocalActLawAndDirectivesComponent } from './local-act-law-and-directives/local-act-law-and-directives.component';
import { TrekkingComponent } from './trekking/trekking.component';
import { PasswordAndVisaComponent } from './password-and-visa/password-and-visa.component';
import { TravelAndTourismInformationComponent } from './travel-and-tourism-information/travel-and-tourism-information.component';
import { MountainComponent } from './mountain/mountain.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { EgovServiceComponent } from './egov-services/egov-service.component';
import { NewsNoticeDetailComponent } from './news-notice-detail/news-notice-detail.component';
import { ReportsComponent } from './reports/reports.component';


export const router: Routes = [
    { path: '', component: IndexComponent },
    { path: 'brief-introduction/:id', component: BriefIntroductionComponent },
    { path: 'village-profile', component: VillageProfileComponent },
    { path: 'organization-structure', component: OrganizationStructureComponent },
    { path: 'committees', component: CommitteesComponent },
    { path: 'reports', component: ReportsComponent },
    { path: 'act-law/:id', component: ActLawComponent },
    { path: 'local-act-law-and-directives', component: LocalActLawAndDirectivesComponent },
    { path: 'trekking', component: TrekkingComponent },
    { path: 'password-and-visa', component: PasswordAndVisaComponent },
    { path: 'travel-and-tourism-information/:id', component: TravelAndTourismInformationComponent },
    { path: 'mountain/:id', component: MountainComponent },
    { path: 'contact-us', component: ContactUsComponent },
    { path: 'egov-service/:id', component: EgovServiceComponent },
    { path: 'news-notice-detail/:id', component: NewsNoticeDetailComponent }
];

export const routes: ModuleWithProviders = RouterModule.forRoot(router, { useHash: true });