import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HeaderService } from './header-service';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  providers: [HeaderService]
})
export class HeaderComponent implements OnInit {
  mountains = [];
  introductions = [];
  reports = [];
  acts = [];
  travelTourism = [];
  egovServices = [];
  environment = environment;
  constructor(private http: HttpClient, private service: HeaderService, private router: Router) {
    this.service.getMountains().subscribe((data: any) => {
      this.mountains = data;
    });

    this.service.getIntroductions().subscribe((data: any) => {
      this.introductions = data;
    });

    this.service.getReports().subscribe((data: any) => {
      this.reports = data;
    });

    this.service.getActLaw().subscribe((data: any) => {
      this.acts = data;
    });

    this.service.getTravelAndTourism().subscribe((data: any) => {
      this.travelTourism = data;
    });

    this.service.getEgovServices().subscribe((data: any) => {
      this.egovServices = data;
    });
  }

  ngOnInit() {
  }

  displayMountain(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/mountain', id]);
  }

  displayIntroduction(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/brief-introduction', id]);
  }

  displayActLaw(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/act-law', id]);
  }

  displayTravelAndTourism(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/travel-and-tourism-information', id]);
  }

  displayEgovServices(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/egov-service', id]);
  }
}
