import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { FooterService } from './footer-service';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss'],
  providers: [FooterService]
})
export class FooterComponent implements OnInit {
  contactDetails = [];
  socialMedia = [];
  copyright = [];
  news = [];
  egovServices = [];
  reports = [];
  environment = environment;

  constructor(private http: HttpClient, private service: FooterService, private router: Router) {
    this.service.getContactDetails().subscribe((data: any) => {
      this.contactDetails = data;
    });

    this.service.getSocialMedia().subscribe((data: any) => {
      this.socialMedia = data;
    });

    this.service.getCopyright().subscribe((data: any) => {
      this.copyright = data;
    });

    this.service.getNews().subscribe((data: any) => {
      this.news = data;
    });

    this.service.getEgovServices().subscribe((data: any) => {
      this.egovServices = data;
    });

    this.service.getReports().subscribe((data: any) => {
      this.reports = data;
    });
  }
  ngOnInit() {
  }
  displayNews(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/news-notice-detail', id]);
  }

  displayEgovServices(event: MouseEvent, id) {
    event.preventDefault();
    this.router.navigate(['/egov-service', id]);
  }
}
